@import url(https://fonts.googleapis.com/css?family=Courgette|Roboto:400,700" rel="stylesheet);
@import url(https://use.fontawesome.com/releases/v5.8.2/css/all.css);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif !important; 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.text-slanted {
  font-family: 'Courgette', cursive;
}

h1 {
  font-size: 30px !important;
  font-weight: 600 !important;
  margin: 20px 0 !important;
}

/* styles */
.page-wrap {
  margin-left: 300px;
  width: calc(100% - 300px);
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
}
.page-wrap.inactive {
  margin-left: 0;
  width: 100%;
}
.navbar {
  background-color: #2F96F3!important;
}
.navbar-light .navbar-brand {
  color: #fff !important;
}
.wrapper {
  width: 300px;
  position: fixed;
  height: 100%;
  background: #f8f9fa;
  left: -300px;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
  z-index: 99;
  box-shadow: 3px 10px 10px #6c757d;
}
.wrapper.active {
  left: 0;
}
.toggle-btn {
  display: none;
}
.toggle-btn span {
  width: 30px;
  height: 4px;
  display: block;
  margin: 5px 0;
  background: #fff;
}
@media screen and (max-width: 991px) {
  .toggle-btn {
    display: block;
  }
  .page-wrap {
    margin-left: 0;
    width: calc(100% - 50px);
  }
}
@media screen and (min-width: 992px) {
  .wrapper {
    margin-left: 0;
    left: 0;
  }
  .page-wrap {
    margin-left: 300px;
    width: calc(100% - 200px);
  }
}
#sidebar .navbar {
  flex-direction: column !important;
  align-items: flex-start;
  background-color: #f8f9fa!important;
}

#sidebar .navbar-nav {
  margin: 0 0 20px 0 !important;
  padding: 0;
  flex-direction: column !important;
  width: 100%;
}

#sidebar .navbar-brand {
  color: #2F96F3 !important;
  text-transform: uppercase;
  font-size: 16px;
}

#sidebar .navbar-nav li {
  border-bottom: 1px solid #999;
  padding: 12px 0 2px;
}

#sidebar .navbar-nav li:last-child {
  border-bottom: none;
  padding: 12px 0 2px;
}

#sidebar .navbar-nav li a {
  color: #343a40;
}

#sidebar .navbar-nav li a:hover {
  text-decoration: none;
}

.close-btn {
  width: 100%;
  text-align: right;
}

@media screen and (min-width: 992px) {
  .close-btn {
    display: none;
  }
}
